// shopping-cart-lambda-stream-to-sns/src/index.ts
import { PublishCommand, SNSClient } from "@aws-sdk/client-sns";
import { v4 } from "uuid";
var snsClient = new SNSClient();
var handler = async (event, context, callback) => {
  event.Records.forEach((record) => {
    if (record.eventName == "INSERT") {
      const newImage = record.dynamodb?.NewImage;
      const event2 = {};
      for (const key in newImage) {
        const valueWithType = newImage[key];
        const value = valueWithType["S"];
        event2[key] = value;
      }
      snsClient.send(
        new PublishCommand({
          TopicArn: process.env.AWS_TOPIC_SHOPPING_CART_EVENTS,
          MessageGroupId: event2["shoppingCartId"],
          MessageDeduplicationId: v4(),
          Message: JSON.stringify(event2)
        })
      ).then(() => console.log("message sent"));
    }
  });
  callback(null, `Successfully processed ${event.Records.length} records.`);
};
export {
  handler
};
